/* 
BUTTON STYLES
Styles for each button type.
*/

a.button,
button.button {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 10ch;
    min-height: 44px;
    padding: 0.7em 1.05em;
    color: var(--clr-text-100);
    text-align: center;
    line-height: 1.1;
    font-weight: var(--fw-semibold);
    background: linear-gradient(142deg, #4C95FA 25%, #1674F9 65%);
    border-radius: var(--radius-xs);
    overflow: hidden;
    z-index: 1;

    
    &::before {
        content: "";
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        background:
            linear-gradient(140deg, #4C95FA 25%, #1674F9 65%) padding-box,
            linear-gradient(170deg, #B4D3FD -30%, transparent 60%) border-box;
        border: 1px solid transparent;
        border-radius: var(--radius-xs);
        transition: all .1s ease-out;
        z-index: -1;
    }

    &:hover::before,
    &:active::before {
        filter: brightness(.9);
    }

    &:focus-visible {
        // outline-style: solid;
        // outline-color: transparent;
        // box-shadow: 0 0 0 4px scale-color(hsl(42, 91%, 58%), $lightness: -40%);
        outline: 3px solid var(--clr-brand-accent);
        outline-offset: 0.25rem;
        border-radius: 2px;
    }
}

a.button {
    text-decoration: none;
}

button.button {
    border: none;
    background-color: transparent;
    font-family: inherit;
    padding: 0;
    cursor: pointer;
}

/* Ghost buttons */
.button-ghost {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0.4em 0.7em;
    color: var(--clr-text-100);
    text-align: center;
    border: 1px solid var(--clr-border-link);
    border-radius: var(--radius-small);
    transition: all .1s ease-out;

    &:hover {
        background: rgba(109 153 248 / 40%);
    }
}