/* 
TYPOGRAPHY
Font styling for headings, paragraphs etc.
*/

:root {
    //Font family
    --font-family: 'MonaSans', Helvetica, Arial, sans-serif;
    --fw-reg: 400;
    --fw-semibold: 600;
    --fw-bold: 700;
}
 // Heading styles
h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-family);
    font-weight: var(--fw-semibold);
    line-height: 1.3;
    color: var(--clr-text-100);
}

// Heading sizes
h1 {font-size: var(--step-6);}
h2 {font-size: var(--step-5);}
h3 {font-size: var(--step-4);}
h4 {font-size: var(--step-3);}
h5 {font-size: var(--step-1);}
h6 {font-size: var(--step-0);}

.section-title {
    line-height: 1;
    color: var(--clr-text-400);
    margin-block-end: var(--space-m);
    padding-inline-start: var(--space-xs);
    border-left: 3px solid var(--clr-brand-accent);
}

// Colour overrides
.clr-100 {
    color: var(--clr-text-100);
}

.clr-400 {
    color: var(--clr-text-400);
}

.clr-900 {
    color: var(--clr-text-900);
}

// Misc font styles
.bold {
    font-weight: var(--fw-bold);
}

.body-small, .small {
    font-size: var(--step--1);
}

// Links
a {
    color: var(--clr-text-100);
    text-decoration-color: transparent;
    text-underline-offset: 4px;

    &:hover,
    &:active {
        text-decoration: underline;
        text-decoration-color: var(--clr-text-link);
    }
    
    &:focus-visible {
        outline: 2px solid var(--clr-brand-accent);
        outline-offset: 0.25rem;
        border-radius: 2px;
    }
}

// Standard text links within body text
:is(.entry-content) a {
    color: var(--clr-text-link);
    text-decoration: underline;

    &:hover,
    &:active {
        color: var(--clr-text-link-hover);
    }
}