@charset "UTF-8";
/*!
Theme Name: Big Blue Box Theme
Theme URI: https://www.bigblueboxpodcast.co.uk
Author: Garry Aylott
Description: A custom theme for The Big Blue Box Podcast website.
Version: 2
Text Domain: bigbluebox
*/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Prevent font size inflation */
html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

/* Remove default margin in favour of better control in authored CSS */
body, h1, h2, h3, h4, h5, p,
figure, blockquote, dl, dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
  padding: 0;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  margin: 0;
}

/* Set shorter line heights on headings and interactive elements */
h1, h2, h3, h4, h5,
button, input, label {
  line-height: 1.1;
}

/* Remove default spacing */
p {
  margin: 0;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input, button,
textarea, select {
  font-family: inherit;
  font-size: inherit;
}

/* Make sure textareas without a rows attribute are not tiny */
textarea:not([rows]) {
  min-height: 10em;
}

/* Anything that has been anchored to should have extra scroll margin */
:target {
  scroll-margin-block: 5ex;
}

:root {
  /* Step -2: 11.1111px → 13.8889px // Footnotes */
  --step--2: clamp(0.6944rem, 0.6341rem + 0.3019vi, 0.8681rem);
  /* Step -1: 13.3333px → 15.0038px // Body small */
  --step--1: clamp(0.8333rem, 0.797rem + 0.1816vi, 0.9377rem);
  /* Step 0: 16px → 20px * // Body default */
  --step-0: clamp(1rem, 0.913rem + 0.4348vi, 1.25rem);
  /* Step 1: 19.2px → 24px // Body large */
  --step-1: clamp(1.2rem, 1.0957rem + 0.5217vi, 1.5rem);
  /* Step 2: 23.04px → 28.8px // Heading H5 */
  --step-2: clamp(1.44rem, 1.3148rem + 0.6261vi, 1.8rem);
  /* Step 3: 27.648px → 34.56px // Heading H4 */
  --step-3: clamp(1.728rem, 1.5777rem + 0.7513vi, 2.16rem);
  /* Step 4: 33.1776px → 48.8281px // Heading H3 */
  --step-4: clamp(2.0736rem, 1.7334rem + 1.7011vi, 3.0518rem);
  /* Step 5: 27.648px → 67.5px // Heading H2 */
  --step-5: clamp(1.728rem, 0.8617rem + 4.3317vi, 4.2188rem);
  /* Step 6: 57.3309px → 95.3674px // Heading H1 */
  --step-6: clamp(3.5832rem, 2.7563rem + 4.1344vi, 5.9605rem);
  /* Space 3xs: 4px → 5px */
  --space-3xs: clamp(0.25rem, 0.2283rem + 0.1087vi, 0.3125rem);
  /* Space 2xs: 6px → 8px */
  --space-2xs: clamp(0.375rem, 0.3315rem + 0.2174vi, 0.5rem);
  /* Space xs: 13px → 16px */
  --space-xs: clamp(0.8125rem, 0.7473rem + 0.3261vi, 1rem);
  /* Space s: 16px → 20px */
  --space-s: clamp(1rem, 0.913rem + 0.4348vi, 1.25rem);
  /* Space m: 24px → 30px */
  --space-m: clamp(1.5rem, 1.3696rem + 0.6522vi, 1.875rem);
  /* Space l: 32px → 40px */
  --space-l: clamp(2rem, 1.8261rem + 0.8696vi, 2.5rem);
  /* Space xl: 48px → 60px */
  --space-xl: clamp(3rem, 2.7391rem + 1.3043vi, 3.75rem);
  /* Space 2xl: 64px → 80px */
  --space-2xl: clamp(4rem, 3.6522rem + 1.7391vi, 5rem);
  /* Space 3xl: 96px → 120px */
  --space-3xl: clamp(6rem, 5.4783rem + 2.6087vi, 7.5rem);
  /* One-up pairs */
  /* Space 3xs-2xs: 4px → 8px */
  --space-3xs-2xs: clamp(0.25rem, 0.163rem + 0.4348vi, 0.5rem);
  /* Space 2xs-xs: 6px → 16px */
  --space-2xs-xs: clamp(0.375rem, 0.1576rem + 1.087vi, 1rem);
  /* Space xs-s: 13px → 20px */
  --space-xs-s: clamp(0.8125rem, 0.6603rem + 0.7609vi, 1.25rem);
  /* Space s-m: 16px → 30px */
  --space-s-m: clamp(1rem, 0.6957rem + 1.5217vi, 1.875rem);
  /* Space m-l: 24px → 40px */
  --space-m-l: clamp(1.5rem, 1.1522rem + 1.7391vi, 2.5rem);
  /* Space l-xl: 32px → 60px */
  --space-l-xl: clamp(2rem, 1.3913rem + 3.0435vi, 3.75rem);
  /* Space xl-2xl: 48px → 80px */
  --space-xl-2xl: clamp(3rem, 2.3043rem + 3.4783vi, 5rem);
  /* Space 2xl-3xl: 64px → 120px */
  --space-2xl-3xl: clamp(4rem, 2.7826rem + 6.087vi, 7.5rem);
  /* Other spacing values */
  --gutter: var(--space-m);
  --flow-space-small: var(--space-xs-2xs);
  --flow-space: var(--space-s-m);
  --flow-space-large: var(--space-l-xl);
  --flow-space-xl: calc(var(--space-2xl-3xl) + var(--space-s));
  --clr-text-100: hsl(0, 0%, 100%);
  --clr-text-400: hsl(218, 14%, 85%);
  --clr-text-900: hsl(219, 11%, 70%);
  --clr-text-link: hsl(221, 91%, 70%);
  --clr-text-link-hover: hsl(221, 91%, 66%);
  --clr-surface-700: hsl(215, 50%, 5%);
  --clr-surface-600: hsl(217, 50%, 7%);
  --clr-surface-400: hsl(224, 16%, 13%);
  --clr-surface-200: hsl(218, 15%, 25%);
  --clr-surface-100: hsl(219, 13%, 38%);
  --clr-border: hsl(219, 11%, 50%);
  --clr-border-darker: var(--clr-surface-100);
  --clr-border-link: hsl(221, 91%, 70%);
  --clr-brand-primary: hsl(215, 95%, 55%);
  --clr-brand-secondary: hsl(42, 91%, 58%);
  --clr-brand-accent: hsl(334, 88%, 68%);
}

/* 
FONTS
Self-hosted font details.
*/
@font-face {
  font-family: "MonaSans";
  font-weight: 400;
  font-style: normal;
  src: url("./fonts/MonaSans-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "MonaSans";
  font-weight: 400;
  font-style: italic;
  src: url("./fonts/MonaSans-Italic.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "MonaSans";
  font-weight: 700;
  font-style: normal;
  src: url("./fonts/MonaSans-Bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "MonaSans";
  font-weight: 600;
  font-style: normal;
  src: url("./fonts/MonaSans-SemiBold.woff2") format("woff2");
  font-display: swap;
}
/* 
GLOBAL STYLES
Site-wide styles.
*/
aside {
  margin-top: 500px;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family);
  font-size: var(--step-0);
  font-weight: var(--fw-reg);
  line-height: 1.5;
  color: var(--clr-text-400);
  background: var(--clr-surface-700);
  overflow-x: hidden;
}
body:is(.home) .page-bg-inline > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

* {
  scrollbar-color: var(--clr-surface-600) #c2c5cc;
}

*::selection {
  background: var(--clr-brand-accent);
  color: var(--clr-surface-700);
  -webkit-text-stroke-width: 0;
}

.post-thumb-img {
  width: 100%;
  aspect-ratio: 16/9;
  object-fit: cover;
  filter: saturate(1) brightness(1);
  transition: filter 0.2s ease-in-out;
}
.post-thumb-img:hover {
  filter: saturate(1.1) brightness(1.1);
}

/* 
TYPOGRAPHY
Font styling for headings, paragraphs etc.
*/
:root {
  --font-family: "MonaSans", Helvetica, Arial, sans-serif;
  --fw-reg: 400;
  --fw-semibold: 600;
  --fw-bold: 700;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-family);
  font-weight: var(--fw-semibold);
  line-height: 1.3;
  color: var(--clr-text-100);
}

h1 {
  font-size: var(--step-6);
}

h2 {
  font-size: var(--step-5);
}

h3 {
  font-size: var(--step-4);
}

h4 {
  font-size: var(--step-3);
}

h5 {
  font-size: var(--step-1);
}

h6 {
  font-size: var(--step-0);
}

.section-title {
  line-height: 1;
  color: var(--clr-text-400);
  margin-block-end: var(--space-m);
  padding-inline-start: var(--space-xs);
  border-left: 3px solid var(--clr-brand-accent);
}

.clr-100 {
  color: var(--clr-text-100);
}

.clr-400 {
  color: var(--clr-text-400);
}

.clr-900 {
  color: var(--clr-text-900);
}

.bold {
  font-weight: var(--fw-bold);
}

.body-small, .small {
  font-size: var(--step--1);
}

a {
  color: var(--clr-text-100);
  text-decoration-color: transparent;
  text-underline-offset: 4px;
}
a:hover, a:active {
  text-decoration: underline;
  text-decoration-color: var(--clr-text-link);
}
a:focus-visible {
  outline: 2px solid var(--clr-brand-accent);
  outline-offset: 0.25rem;
  border-radius: 2px;
}

:is(.entry-content) a {
  color: var(--clr-text-link);
  text-decoration: underline;
}
:is(.entry-content) a:hover, :is(.entry-content) a:active {
  color: var(--clr-text-link-hover);
}

/* 
HOMEPAGE
Styling for the homepage.
*/
/* 
REGION UTILITY
Consistent block padding for page sections.
*/
.region-small {
  padding-block: var(--region-space-small, var(--space-m-l));
}

.region-normal {
  padding-block: var(--region-space-normal, var(--space-xl-2xl));
}

.region-large {
  padding-block: var(--region-space, var(--space-2xl-3xl));
}

.region-bottom {
  padding-block-end: var(--region-space, var(--space-xl-2xl));
}

/* 
FLOW UTILITY
Spacing value between content blocks.
*/
.flow-small > * + * {
  margin-block-start: var(--flow-space-small, 0.5em);
}

.flow > * + * {
  margin-block-start: var(--flow-space, 1em);
}

.flow-large > * + * {
  margin-block-start: var(--flow-space-large, 2em);
}

.flow-page-regions > * + * {
  margin-block-start: var(--flow-space-xl, 8em);
}

/* 
WRAPPER UTILITY
Settings for the overall content width.
*/
.wrapper {
  margin-inline: auto;
  max-width: clamp(16rem, var(--wrapper-max-width, 100vw), 80rem);
  padding-left: var(--gutter);
  padding-right: var(--gutter);
  position: relative;
}

/* 
VISUALLY HIDEN UTILITY
Modern way of properly hiding elements.
*/
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 0;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

/* 
BORDER RADIUS UTILITY
Values for consistent border radius.
*/
:root {
  --radius-xs: 6px;
  --radius-small: 8px;
  --radius-normal: 16px;
  --radius-large: 48px;
}

.rounded {
  border-radius: var(--radius-normal);
}

.rounded-small {
  border-radius: var(--radius-small);
}

/* 
ICON TEXT GROUPS UTILITY
Groupings for text that have either leading or ttailing icons.
*/
p.icon-text-group {
  display: flex;
  gap: var(--space-2xs);
  align-items: center;
}
p.icon-text-group svg {
  fill: currentColor;
}

/* 
BG BLUR UTILITY
Applies background colour and blur to an element.
** Remeber to add position: relative to the element.**
*/
.bg-blur::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  background: hsla(221, 90%, 85%, 0.1);
  backdrop-filter: blur(0.3rem);
  z-index: -1;
}

/* 
VARIOUS FLEX LAYOUT CLASSES
*/
.flex-splitter {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flex-push-right {
  justify-content: flex-end;
}

/* 
HEADER BLOCK
Header styling for logo, nav etc.
*/
.site-head {
  position: relative;
  padding: var(--space-xs) 0;
}
.site-head .header-backdrop {
  position: absolute;
  top: 0;
  width: 100%;
  opacity: 1;
  backdrop-filter: blur(15px);
  background: hsla(218, 15%, 25%, 0.07);
  inset: 0;
  z-index: -1;
}
.site-head__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.site-head__logo img {
  width: clamp(11rem, 78vw - 3.7rem, 17rem);
  height: auto;
}
.site-head__navigation {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
  text-align: right;
}
.site-head__navigation p {
  font-size: var(--step--1);
}
.site-head__navigation .nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: var(--space-xl);
  justify-content: flex-end;
  font-size: calc(var(--step--1) + 0.15rem);
  font-weight: var(--fw-bold);
  margin: 0;
}
.site-head__navigation .menu-nav-toggle {
  display: none;
}
@media (max-width: 53em) {
  .site-head__navigation p {
    display: none;
  }
  .site-head .nav {
    position: fixed;
    width: 80%;
    height: 100vh;
    inset: 0 0 0 100%;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: var(--space-3xl) var(--space-l);
    gap: var(--space-l);
    font-size: var(--step-1);
    background: var(--clr-surface-400);
    z-index: 1000;
    transform: translateX(0);
    transition: transform 0.25s ease-out;
  }
  .site-head .nav[data-visible=true] {
    transform: translateX(-100%);
  }
  .site-head .menu-nav-toggle {
    display: block;
    position: absolute;
    right: 1.75rem;
    width: 2.25rem;
    aspect-ratio: 1;
    background: none;
    border: none;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 9999;
  }
  .site-head .menu-nav-toggle::before {
    content: "";
    display: block;
    position: absolute;
    top: -20px;
    right: -10px;
    bottom: -20px;
    left: -30px;
  }
  .site-head .menu-nav-toggle span {
    position: absolute;
    top: 50%;
    display: block;
    width: 100%;
    height: 5px;
    background-color: var(--clr-text-100);
    transition: 0.25s ease-out;
  }
  .site-head .menu-nav-toggle span:nth-of-type(1) {
    top: 30%;
  }
  .site-head .menu-nav-toggle span:nth-of-type(2) {
    top: 70%;
  }
  .site-head .menu-nav-toggle[aria-expanded=true] {
    position: fixed;
  }
  .site-head .menu-nav-toggle[aria-expanded=true] span {
    background-color: var(--clr-brand-secondary);
  }
  .site-head .menu-nav-toggle[aria-expanded=true] span:nth-of-type(1) {
    top: 50%;
    transform: rotate(45deg);
  }
  .site-head .menu-nav-toggle[aria-expanded=true] span:nth-of-type(2) {
    top: 50%;
    transform: rotate(-45deg);
  }
}

/* 
PROSE BLOCK
Default value for spacing between text paragraphs.
*/
.prose {
  --flow-space: var(--space-m);
}

/* 
LATEST PODCAST EPISODE BLOCK
*/
.latest-podcast-ep {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  gap: var(--space-m);
  row-gap: var(--space-s);
  margin-block-start: var(--space-2xl-3xl);
}
.latest-podcast-ep__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.latest-podcast-ep__content a {
  display: block;
}
.latest-podcast-ep__content p {
  color: var(--clr-text-400);
}
.latest-podcast-ep__details {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
}

/* 
BUTTON STYLES
Styles for each button type.
*/
a.button,
button.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 10ch;
  min-height: 44px;
  padding: 0.7em 1.05em;
  color: var(--clr-text-100);
  text-align: center;
  line-height: 1.1;
  font-weight: var(--fw-semibold);
  background: linear-gradient(142deg, #4C95FA 25%, #1674F9 65%);
  border-radius: var(--radius-xs);
  overflow: hidden;
  z-index: 1;
}
a.button::before,
button.button::before {
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(140deg, #4C95FA 25%, #1674F9 65%) padding-box, linear-gradient(170deg, #B4D3FD -30%, transparent 60%) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  transition: all 0.1s ease-out;
  z-index: -1;
}
a.button:hover::before, a.button:active::before,
button.button:hover::before,
button.button:active::before {
  filter: brightness(0.9);
}
a.button:focus-visible,
button.button:focus-visible {
  outline: 3px solid var(--clr-brand-accent);
  outline-offset: 0.25rem;
  border-radius: 2px;
}

a.button {
  text-decoration: none;
}

button.button {
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
}

/* Ghost buttons */
.button-ghost {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.4em 0.7em;
  color: var(--clr-text-100);
  text-align: center;
  border: 1px solid var(--clr-border-link);
  border-radius: var(--radius-small);
  transition: all 0.1s ease-out;
}
.button-ghost:hover {
  background: rgba(109, 153, 248, 0.4);
}

/* 
LINKS TO PODCAST APPS BLOCK
*/
.podcast-app-links {
  position: relative;
  width: fit-content;
}
.podcast-app-links__buttons {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-xs);
}
.podcast-app-links__buttons .link-column a {
  display: flex;
  min-height: 45px;
}
@media (max-width: 520px) {
  .podcast-app-links__buttons .link-column {
    flex: 45%;
  }
  .podcast-app-links__buttons a {
    flex-grow: 1;
  }
}
@media (max-width: 400px) {
  .podcast-app-links__buttons {
    flex-direction: column;
  }
}
.podcast-app-links .pod-links-menu details summary {
  list-style: none;
  cursor: pointer;
}
.podcast-app-links .pod-links-menu details[open] summary {
  background: rgba(109, 153, 248, 0.4);
  border-radius: var(--radius-small);
}
.podcast-app-links .pod-links-menu .other-pod-links-popup {
  --popover-spacing: calc(var(--space-xs) - 0.3rem);
  position: absolute;
  bottom: 130%;
  right: 0;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: var(--popover-spacing);
  padding: var(--popover-spacing);
  background: white;
  z-index: 100;
}
.podcast-app-links .pod-links-menu .other-pod-links-popup__item img {
  width: 3.5rem;
  filter: grayscale(1) brightness(0.9);
  transition: filter 0.2s ease-out;
}
.podcast-app-links .pod-links-menu .other-pod-links-popup__item img:hover {
  filter: grayscale(0) brightness(1);
}

/* 
AUTHOR META BLOCK
Author image and post meta styling.
*/
.author-meta {
  display: grid;
  grid-template-columns: 3rem 1fr;
  align-items: center;
  gap: var(--space-xs);
  padding-block-start: var(--space-s);
}
.author-meta__details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* 
CATEGORY TAG BLOCK
*/
.category-tag {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2xs);
  padding: 0.25rem 0.625rem;
  background: var(--clr-surface-400);
}

/* 
LATEST ARTICLES BLOCK
*/
.latest-articles-featured {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(19rem, 1fr));
  gap: var(--space-m);
}
.latest-articles-featured article > * + * {
  margin-block-start: 0.85rem;
}
.latest-articles-featured article footer {
  margin-block-start: 0;
}

/* 
TESTIMONIALS STYLES
Styles for full-width and column based testimonials.
*/
blockquote {
  font-style: italic;
}
blockquote em {
  position: relative;
  color: var(--clr-surface-700);
  background: var(--clr-brand-secondary);
  padding: 0 3px 3px 6px;
  margin-inline-start: -4px;
}

.testimonial-fw {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
  width: 90%;
  padding-inline-start: var(--space-m);
  margin-inline: auto;
  border-left: 4px solid var(--clr-brand-secondary);
}
@media (max-width: 53em) {
  .testimonial-fw {
    width: 100%;
  }
}
.testimonial-fw h4, .testimonial-fw h5, .testimonial-fw p {
  color: var(--clr-text-400);
}
.testimonial-fw__source {
  display: flex;
  flex-direction: column;
}
.testimonial-fw__source p, .testimonial-fw__source cite {
  font-style: normal;
}
.testimonial-fw__source cite {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2xs);
  font-weight: var(--fw-bold);
}
.testimonial-fw__bg-image {
  position: absolute;
  top: -65%;
  right: 0;
  width: 250px;
  height: auto;
  opacity: 0.7;
  z-index: -1;
}
@media (max-width: 53em) {
  .testimonial-fw__bg-image {
    display: none;
  }
}